import MXRUtil from '../../common/mxrutil.js'
const requestUrl = MXRUtil.MXRConstant.serverHost + '/book/detail/9AC6577B5A444E4A9810119C6A4DFBF7'
console.log('userId : ', MXRUtil.userId)
export default {
	data() {
		return {
			title: 'Hello',
			hasBanner: false,
			bannerArray: [],
			dataArray: [],
			hasMenu: false,
			menuArray: [],
			isLoadingShow: true,
			loadingType: 'trip'
		}
	},
	onLoad() {
		uni.navigateTo({
			url:"lanchpage",
			animationType:"slide-in-right",
			animationDuration:500
		})
		
	},
	onShow() {
		console.log('bookstore on show')
		if (this.dataArray == null || this.dataArray.length <= 0) {
			this._request()
		}
	},
	methods: {
		_request() {
			MXRUtil.mxr_get('/core/home/0', {
				page: '1',
				rows: '20',
				search: 'normal',
				topNums: '20',
				region: '0',
				uid: MXRUtil.userId,
				deviceId: '5F3E6EB4-CA01-42B0-BD77-0E72DA409618'
			}, (err, res) => {
				if (err) {
					console.log('>>> err: ', err)
				} else {
					this.dataArray = res.data.Body.list
					console.log('>>> res: ', res.data)
				}
			})
		},
		goBookDetailPage(bookGuid) {
			uni.navigateTo({
				url: "bookdetail?bookGuid=" + bookGuid
			})
		}
	}
}
